import PropTypes from "prop-types";
import * as Yup from "yup";
import { useEffect, useMemo, useCallback, useState } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
// form
import { useForm, useFieldArray } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { LoadingButton } from "@mui/lab";
import {
  Box,
  Card,
  Grid,
  Stack,
  Typography,
  Button,
  Divider,
  MenuItem,
} from "@mui/material";
// utils
import { fData } from "../../../utils/formatNumber";
import { fDateOnly } from "../../../utils/formatTime";
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// components
import Label from "../../../components/Label";
import Iconify from "../../../components/Iconify";
import {
  FormProvider,
  RHFSelect,
  RHFSwitch,
  RHFTextField,
  RHFUploadAvatar,
  RHFDateField,
  RHFPhoneField,
} from "../../../components/hook-form";
import { useDispatch, useSelector } from "../../../redux/store";
import {
  getGenres,
  getMaritals,
  getCountries,
  getTypeIds,
} from "../../../redux/slices/generale";
import {
  addSupervisor,
  updateSupervisor,
  addSupervisorSuccess,
  updateSupervisorSuccess,
} from "../../../redux/slices/supervisor";
import useIsMountedRef from "../../../hooks/useIsMountedRef";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

SupervisorNewEditForm.propTypes = {
  isEdit: PropTypes.bool,
  currentSupervisor: PropTypes.object,
};

export default function SupervisorNewEditForm({ isEdit, currentSupervisor }) {
  const navigate = useNavigate();

  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();

  const NationalIdShema = Yup.object().shape({
    type_id: Yup.string().required(
      t("field.required", {
        obj: t("field.type_id"),
      })
    ),
    number_id: Yup.string().required(
      t("field.required", {
        obj: t("field.number_id"),
      })
    ),
    created_on: Yup.string().required(
      t("field.required", {
        obj: t("field.created_on"),
      })
    ),
    expired_on: Yup.string().required(
      t("field.required", {
        obj: t("field.expired_on"),
      })
    ),
  });

  const NewSupervisorSchema = Yup.object().shape({
    // username: Yup.string().required(
    //   t("field.required", {
    //     obj: t("field.username"),
    //   })
    // ),
    phone: Yup.string().required(
      t("field.required", {
        obj: t("field.phone"),
      })
    ),
    first_name: Yup.string().required(
      t("field.required", {
        obj: t("field.first_name"),
      })
    ),
    last_name: Yup.string().required(
      t("field.required", {
        obj: t("field.last_name"),
      })
    ),
    percent: Yup.number().required(
      t("field.required", {
        obj: t("field.percent"),
      })
    ),
    // user_pieces_ids: Yup.array()
    //   .of(NationalIdShema)
    //   .min(1, "Must have at least one id")
    //   .max(3, "That is too many id"),
  });

  const defaultValues = useMemo(
    () => ({
      username: currentSupervisor?.username || "",
      email: currentSupervisor?.email || "",
      phone: currentSupervisor?.phone || "",
      first_name: currentSupervisor?.first_name || "",
      last_name: currentSupervisor?.last_name || "",
      photo: currentSupervisor?.photo || null,
      country: currentSupervisor?.country || "",
      city: currentSupervisor?.city || "",
      address: currentSupervisor?.address || "",
      born_date: fDateOnly(currentSupervisor?.born_date || new Date()),
      is_block: currentSupervisor?.is_block || false,
      genre: currentSupervisor?.genre || "",
      marital: currentSupervisor?.marital || "",
      description: currentSupervisor?.description || "",
      password: null,
      percent: currentSupervisor?.percent || 0,
      // user_pieces_ids: currentSupervisor?.user_pieces_ids?.map((item) => ({
      //   ...item,
      //   created_on: fDateOnly(item.created_on),
      //   expired_on: fDateOnly(item.expired_on),
      // })) || [
      //   {
      //     type_id: "",
      //     number_id: "",
      //     created_on: new Date(),
      //     expired_on: new Date(),
      //   },
      // ],
      keyId: currentSupervisor?.uid || -1,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentSupervisor]
  );

  const methods = useForm({
    resolver: yupResolver(NewSupervisorSchema),
    defaultValues,
  });

  const dispatch = useDispatch();

  const isMountedRef = useIsMountedRef();

  const {
    reset,
    watch,
    control,
    setError,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (isEdit && currentSupervisor) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentSupervisor]);

  const onSubmit = async () => {
    try {
      const formData = new FormData();

      Object.entries(values).forEach(([key, value]) => {
        if (!["photo", "percent", "keyId"].includes(key)) {
          if (key === "born_date") {
            formData.append(key, fDateOnly(value));
          } else {
            formData.append(key, value);
          }
        }
      });

      if (open) {
        formData.append("photo", values.photo);
      }
      formData.append("percent", values.percent);
      formData.append("keyId", values.keyId);
      if (isEdit) {
        const results = await updateSupervisor(formData);
        dispatch(updateSupervisorSuccess(results));
      } else {
        const results = await addSupervisor(formData);
        dispatch(addSupervisorSuccess(results));
      }
      setOpen(false);
      reset();
      enqueueSnackbar(!isEdit ? t("success.CREATE") : t("success.UPDATED"));
      navigate(PATH_DASHBOARD.supervisor.list);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
      if (isMountedRef.current) {
        if (error.response?.data?.phone) {
          setError("phone", { ...error, message: error.response?.data?.phone });
        }
        if (error.response?.data?.born_date) {
          setError("born_date", {
            ...error,
            message: error.response?.data?.born_date,
          });
        }
        if (error.response?.data?.username) {
          setError("username", {
            ...error,
            message: error.response?.data?.username,
          });
        }
        if (error.response.data.email) {
          setError("email", { ...error, message: error.response.data.email });
        } else if (error.response.data.non_field_errors) {
          setError("email", {
            ...error,
            message: error.response.data.non_field_errors,
          });
        }
        if (error.response?.data?.photo) {
          setError("photo", { ...error, message: error.response?.data?.photo });
        }
      }
    }
  };

  const { countries, genres, maritals, usertypeids } = useSelector(
    (state) => state.generale
  );

  useEffect(() => {
    dispatch(getGenres());
    dispatch(getMaritals());
    dispatch(getCountries());
    dispatch(getTypeIds());
  }, [dispatch]);

  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setValue(
          "photo",
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
      setOpen(true);
    },
    [setValue]
  );

  const { fields, append, remove } = useFieldArray({
    control,
    name: "user_pieces_ids",
  });

  const handleAdd = () => {
    append({
      type_id: "",
      number_id: "",
      created_on: new Date(),
      expired_on: new Date(),
    });
  };

  const handleRemove = (index) => {
    remove(index);
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card sx={{ py: 10, px: 3 }}>
            {isEdit && (
              <Label
                color={values.is_block ? "error" : "success"}
                sx={{
                  textTransform: "uppercase",
                  position: "absolute",
                  top: 24,
                  right: 24,
                }}
              >
                {values.is_block ? "Banned" : "Active"}
              </Label>
            )}

            <Box sx={{ mb: 5 }}>
              <RHFUploadAvatar
                name="photo"
                accept="image/*"
                maxSize={3145728}
                onDrop={handleDrop}
                helperText={
                  <Typography
                    variant="caption"
                    sx={{
                      mt: 2,
                      mx: "auto",
                      display: "block",
                      textAlign: "center",
                      color: "text.secondary",
                    }}
                  >
                    Allowed *.jpeg, *.jpg, *.png, *.gif
                    <br /> max size of {fData(3145728)}
                  </Typography>
                }
              />
            </Box>

            {isEdit && (
              <RHFSwitch
                name="is_block"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Banned
                    </Typography>
                    <Typography
                      variant="body2"
                      sx={{ color: "text.secondary" }}
                    >
                      Apply disable account
                    </Typography>
                  </>
                }
                sx={{ mx: 0, mb: 3, width: 1, justifyContent: "space-between" }}
              />
            )}

            {/* <RHFSwitch
              name="is_owner"
              labelPlacement="start"
              label={
                <>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                    Master Gold Verified
                  </Typography>
                  <Typography variant="body2" sx={{ color: "text.secondary" }}>
                    Disabling this will automatically send the Master Agent a
                    verification Master Gold
                  </Typography>
                </>
              }
              sx={{ mx: 0, width: 1, justifyContent: "space-between" }}
            /> */}
          </Card>
        </Grid>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              {/* <RHFTextField name="username" label={t("field.username")} /> */}
              <RHFTextField
                type="email"
                name="email"
                label={t("field.email")}
              />
              <RHFPhoneField name="phone" label={t("field.phone")} />
              <RHFTextField name="first_name" label={t("field.first_name")} />
              <RHFTextField name="last_name" label={t("field.last_name")} />
              <RHFDateField
                name="born_date"
                maxDate={new Date()}
                label={t("field.born_date")}
              />
              <RHFSelect
                name="genre"
                label={t("field.genre")}
                placeholder={t("field.genre")}
              >
                <option value="" />
                {genres?.map((option) => (
                  <option key={option} value={option}>
                    {t(`genre.${option}`)}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect
                name="marital"
                label={t("field.marital")}
                placeholder={t("field.marital")}
              >
                <option value="" />
                {maritals?.map((option) => (
                  <option key={option} value={option}>
                    {t(`marital.${option}`)}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect
                name="country"
                label={t("field.country")}
                placeholder={t("field.country")}
              >
                <option value="" />
                {countries?.map((option) => (
                  <option key={option.id} value={option.id}>
                    {t(`country.${option.name}`)}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="city" label={t("field.city")} />
              <RHFTextField name="address" label={t("field.address")} />
              <RHFTextField name="percent" label={t("field.percent")} />
              <RHFTextField name="password" label={t("field.password")} />
              <RHFTextField
                name="description"
                multiline
                rows={4}
                label="Description"
              />
            </Box>
            {/* <Divider flexItem sx={{ borderStyle: "dashed", py: 2 }} />
            <Box sx={{ py: 3 }}>
              <Typography variant="h6" sx={{ color: "text.secondary", mb: 3 }}>
                {t("params.line_of_id")} :
              </Typography>

              <Stack
                divider={<Divider flexItem sx={{ borderStyle: "dashed" }} />}
                spacing={3}
              >
                {fields.map((item, index) => (
                  <Stack key={item.id} alignItems="flex-start" spacing={1.5}>
                    <Stack
                      sx={{
                        display: "grid",
                        columnGap: 2,
                        rowGap: 3,
                        gridTemplateColumns: {
                          xs: "repeat(1, 1fr)",
                          sm: "repeat(2, 1fr)",
                        },
                      }}
                    >
                      <RHFSelect
                        name={`user_pieces_ids[${index}].type_id`}
                        label={t("field.type_id")}
                        size="small"
                        InputLabelProps={{ shrink: true }}
                        SelectProps={{
                          native: false,
                          sx: { textTransform: "capitalize" },
                        }}
                      >
                        {usertypeids.map((option) => (
                          <MenuItem
                            key={option}
                            value={option}
                            sx={{
                              mx: 1,
                              my: 0.5,
                              borderRadius: 0.75,
                              typography: "body2",
                              textTransform: "capitalize",
                            }}
                          >
                            {t(`typeid.${option}`)}
                          </MenuItem>
                        ))}
                      </RHFSelect>

                      <RHFTextField
                        size="small"
                        name={`user_pieces_ids[${index}].number_id`}
                        label={t("field.number_id")}
                        InputLabelProps={{ shrink: true }}
                      />
                      <RHFDateField
                        name={`user_pieces_ids[${index}].created_on`}
                        maxDate={new Date()}
                        label={t("field.created_on")}
                      />
                      <RHFDateField
                        name={`user_pieces_ids[${index}].expired_on`}
                        minDate={new Date()}
                        label={t("field.expired_on")}
                      />
                    </Stack>

                    <Button
                      size="small"
                      color="error"
                      startIcon={<Iconify icon="eva:trash-2-outline" />}
                      onClick={() => {
                        const confirmation = window.confirm(
                          t("params.deletes_alert")
                        );

                        if (confirmation) {
                          handleRemove(index);
                        }
                      }}
                    >
                      {t("params.delete")}
                    </Button>
                  </Stack>
                ))}
              </Stack>

              <Divider sx={{ my: 3, borderStyle: "dashed" }} />

              <Stack
                spacing={2}
                direction={{ xs: "column-reverse", md: "row" }}
                alignItems={{ xs: "flex-start", md: "center" }}
              >
                <Button
                  size="small"
                  startIcon={<Iconify icon="eva:plus-fill" />}
                  onClick={handleAdd}
                  sx={{ flexShrink: 0 }}
                >
                  {t("params.add_new_id")}
                </Button>
              </Stack>
            </Box> */}

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {!isEdit
                  ? t("params.create", {
                      obj: t("p_supervisor.supervisor"),
                    })
                  : t("params.save_change")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
