import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useLocation, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
import { useDispatch } from "../../redux/store";
// hooks
import useSettings from "../../hooks/useSettings";
import { getSupervisor, getSupervisors } from "../../redux/slices/supervisor";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
// sections
import SupervisorNewEditForm from "../../sections/@dashboard/supervisor/SupervisorNewEditForm";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function SupervisorCreate() {
  const { themeStretch } = useSettings();

  const { t } = useTranslation();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const { pathname } = useLocation();

  const { supervisorKey = "" } = useParams();

  const isEdit = pathname.includes("edit");

  const [currentSupervisor, setCurrentSupervisor] = useState(null);

  useEffect(() => {
    if (isEdit) {
      handleGetObject();
    }
    dispatch(getSupervisors(1, 1));
  }, [supervisorKey]);

  const handleGetObject = async () => {
    try {
      const result = await getSupervisor(supervisorKey);
      setCurrentSupervisor(result);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.supervisor.supervisor);
    }
  };

  return (
    <Page
      title={`Configuration: ${
        !isEdit
          ? t("p_supervisor.new_supervisor")
          : t("p_supervisor.edit_supervisor")
      }`}
    >
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={
            !isEdit
              ? t("p_supervisor.new_supervisor")
              : t("p_supervisor.edit_supervisor")
          }
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("p_supervisor.supervisor"),
              href: PATH_DASHBOARD.supervisor.root,
            },
            {
              name: !isEdit
                ? t("p_supervisor.new_supervisor")
                : `${t("p_supervisor.supervisor")} ${
                    currentSupervisor?.full_name
                  }`,
            },
          ]}
        />

        <SupervisorNewEditForm
          isEdit={isEdit}
          currentSupervisor={currentSupervisor}
        />
      </Container>
    </Page>
  );
}
